<template>
    <div class="row">
		<div class="col-md-7 col-sm-12">
			<img src="../../public/img/boolflix.gif" alt="">
		</div>
				
		<div class="ml-5 col-md-5 col-sm-12">
			<p>
				In questo progetto ho realizzato una replica di Netflix, realizzata con HTML/CSS/JAVASCRIPT e utilizzando il framework VUE.js.<br><br>
				Nel progetto ho utilizzato anche Axios per fare chiamate alla API di "The Movie DB" per prelevare le informazioni di film e <span id="dots">...</span><span id="more"> serie TV e mostrarle all'utente.<br><br>
				Inoltre ho inserito una barra di ricerca in alto per ricercare i film presenti nel database.</span> 
			</p>
			<button @click="readMore()" id="myBtn">Leggi di più</button>
		</div>
    </div>
</template>

<script>
export default {
    methods:{
		readMore() {
			var dots = document.getElementById("dots");
			var moreText = document.getElementById("more");
			var btnText = document.getElementById("myBtn");

			if (dots.style.display === "none") {
				dots.style.display = "inline";
				btnText.innerHTML = "Leggi di più"; 
				moreText.style.display = "none";
			} else {
				dots.style.display = "none";
				btnText.innerHTML = "Leggi di meno"; 
				moreText.style.display = "inline";
			}
		}
	}
}
</script>

<style scoped>
* {
	background-color: #233b4f;
	color: white;
}
img{
	width: 100%;
}
#more {
	display: none;
}
</style>