<template>
	<div class="home">
		<div class="jumbotron ">
			<h1 class="animate__animated animate__fadeIn animate__slower">
				Progetti
			</h1>
		</div>
		<div class="container">
			<boolflix></boolflix>
			<div class="margin"></div>
			<boolzap></boolzap>
			
			
			
		</div>
	</div>
</template>

<script>
import Boolflix from './Boolflix.vue';
import Boolzap from './Boolzap.vue';



export default {
	components:{
		Boolflix,
		Boolzap
	},
	methods:{
		readMore() {
			var dots = document.getElementById("dots");
			var moreText = document.getElementById("more");
			var btnText = document.getElementById("myBtn");

			if (dots.style.display === "none") {
				dots.style.display = "inline";
				btnText.innerHTML = "Leggi di più"; 
				moreText.style.display = "none";
			} else {
				dots.style.display = "none";
				btnText.innerHTML = "Leggi di meno"; 
				moreText.style.display = "inline";
			}
		}
	}
};
</script>

<style scoped>
* {
	background-color: #233b4f;
	color: white;
}
h1 {
	padding: 3rem 0;
	text-align: center;
}

.margin{
	margin: 5rem;
}
</style>
