<template>
    <div class="row">
		<div class="col-md-7 col-sm-12">
			<img src="../../public/img/boolzap.gif" alt="">
		</div>
				
		<div class="ml-5 col-md-5 col-sm-12">
			<p>
				In questo progetto ho realizzato una replica di Whatsapp, realizzata con HTML/CSS/JAVASCRIPT e utilizzando il framework VUE.js. senza server<br><br>
				Nello specifico ho ricreato ipotetiche conversazioni, filtrare gli utenti per nome attraverso <span id="dot">...</span><span id="mor"> la barra di ricerca e poter scrivere a ogni utente.<br>
				Cliccando sulle emoji se no possono visulizzare, scegliere e inviarle all'utente desiderato.
				</span> 
			</p>
			<button @click="read()" id="myBt">Leggi di più</button>
		</div>
	</div>
</template>

<script>
export default {
    methods:{
		read() {
			var dot = document.getElementById("dot");
			var morText = document.getElementById("mor");
			var btnText = document.getElementById("myBt");

			if (dot.style.display === "none") {
				dot.style.display = "inline";
				btnText.innerHTML = "Leggi di più"; 
				morText.style.display = "none";
			} else {
				dot.style.display = "none";
				btnText.innerHTML = "Leggi di meno"; 
				morText.style.display = "inline";
			}
		}
	}
}
</script>

<style scoped>
* {
	background-color: #233b4f;
	color: white;
}
img{
	width: 100%;
}
#mor {
	display: none;
}
div .row{
	padding-bottom: 3rem;
}
</style>