<template>
	<div>
		<div class="navbar-website">
			<div class="container">
				<div class="row d-flex align-items-center">
					<!-- burger code -->
					<!-- end burger code  -->
					<div class="col-lg-7 col-md-5 col-sm-3 small-screen-property">
						<div class="square">
							<img src="../../public/img/image.jpeg" alt="" />
						</div>
						<nav id="burger" @click="burgerMenu">
							<div class="linea"></div>
							<div class="linea"></div>
							<div class="linea"></div>
						</nav>
					</div>
					<div class="col-lg-5 col-md-7 col-sm-9 menu ">
						<ul class="">
							<li><router-link to="/">Home</router-link></li>
							<li><router-link to="/chisono">Chi sono</router-link></li>
							<li><router-link to="/contatti">Contatti</router-link></li>
							<li><router-link to="/progetti">Progetti</router-link></li>
						</ul>
					</div>
				</div>
			</div>
		</div>
		<router-view />
	</div>
</template>

<script>
export default {
	name: "Header",
	methods: {
		burgerMenu() {
			let linea1 = document.querySelector("#burger div:nth-child(1)");
			let linea2 = document.querySelector("#burger div:nth-child(2)");
			let linea3 = document.querySelector("#burger div:nth-child(3)");
			linea1.classList.toggle("l1");
			linea2.classList.toggle("l2");
			linea3.classList.toggle("l3");
			let menu = document.querySelector(".menu");
			menu.classList.toggle("inactive");
		},
	},
};
</script>

<style scoped>
.navbar-website {
	background-color: #1a2b3c;
	padding: 1rem;
}
img {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	object-fit: cover;
}
.square {
	width: 80px;
	height: 80px;
}

ul {
	margin: 0;
	padding: 0;
	display: flex;
	justify-content: space-between;
}
ul li {
	list-style: none;
}
ul li a {
	position: relative;
	display: block;
	color: transparent;
	-webkit-text-stroke: 1px white;
	font-size: 20px;
	font-weight: 900;
	text-decoration: none;
	text-transform: capitalize;
}

a:before,
a:after {
	content: attr(data-text);
	position: absolute;
	top: 0;
	left: 0;
	transition: 0.5s;
}
a:hover:before {
	color: rgb(187, 0, 0);
	z-index: 1;
	-webkit-text-stroke: 1px black;
	transform: translate(10px, -10px);
}
a:hover:after {
	color: rgb(119, 239, 255);
	z-index: 2;
	-webkit-text-stroke: 1px black;
	transform: translate(20px, -20px);
}

a:hover {
	color: white;
	-webkit-text-stroke: 1px black;
	transition: 0.5s;
}
/* burger style  */
#burger {
	width: 30px;
	display: none;
}
.linea {
	transition: all 1s;
	margin-top: 5px;
	height: 5px;
	background-color: white;
	width: 30px;
}
.l1 {
	transition: all 1s;
	transform-origin: top left;
	transform: rotate(45deg);
}
.l3 {
	transition: all 1s;
	transform-origin: bottom left;
	transform: rotate(-45deg);
}
.l2 {
	transition: all 1s;
	transform-origin: bottom left;
	transform: rotate(45deg);
	position: relative;
	top: -8.012px;
	width: 10px;
}

@media screen and (max-width: 576px) {
	#burger {
		display: inline;
	}
	.menu {
		display: none;
	}

	.small-screen-property {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	ul {
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
	}
	.inactive {
		transition: all 1s;
		opacity: 1;
		display: flex;
		width: 100%;
		border-radius: 10px;
		height: 100%;
	}
}
</style>
